import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import login from '../components/web/login/login.vue'
import Hello from '../components/Hello.vue'

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component:login

    },
    {
      path: '/',
      name: 'Hello',
      component:Hello

    }
  ]
})



